import React, { Component } from "react";
import { Col, Row } from "reactstrap";

import "./SearchTags.css";

class SearchTags extends Component {
    constructor() {
        super();
        this.state = {
            tags : [
                "People",
                "No People",
                "Happiness",
                "Sadness"
            ]
        }
    }

    render() {
        return (
            <div className="animated fadeIn container container">
                <div onScroll={this.handleScroll}>
                    <div className="row heading">
                        <div className="col">
                            <div className="row py-3">
                                <div className="col">
                                    <div className="row">
                                        <div className="col d-flex justify-content-center">
                                            <h3>View all images tagged with People</h3>                                        
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
    
                <div className="row renderImages-row">
                    <div className="col d-flex justify-content-center">
                        <strong>Tags : </strong> 
                        {this.renderTags()}
                    </div>
                </div>
            </div>
        )
    }

    renderTags () {
        return (
            <span>
                {this.state.tags.map(function(tag, idx) {
                    return ( 
                        <span className="px-1 tag" key={idx}>{tag}</span>
                    )
                })}
            </span>
        )
    }
}

export default SearchTags;